<template>
    <div class="step_three_addition">
        <div class="container_height">
        <div class="container">
            <div class="pickup">
                <input type="checkbox" v-model="pickupOption" @click="checkboxLogic()">Odbiór<br>osobisty
            </div>
        <div v-if="deliveryOption">
            <div class="delivery_form">
                Imię*
                <input id="name" v-model="delivery.name">
                Nazwisko*
                <input id="surname" v-model="delivery.surname">
                Ulica Budynek/Mieszkanie*
                <input id="adress" v-model="delivery.adress">
                Kod Pocztowy*
                <input id="postalCode" v-model="delivery.postalCode">
                Miasto*
                <input id="city" v-model="delivery.city">
                Telefon
                <input id="phone" v-model="delivery.phone">
                Aders mailowy*
                <input id="email" v-model="delivery.email">
                * Pola wymagane
            </div>
        </div>
        <div class="delivery">
                <input type="checkbox" v-model="deliveryOption" @click="checkboxLogic()">Wysyłka<br>
                <button class="fill_delivery" v-if="deliveryOption" @click="fillDelivery()">Adres taki sam <br>jak adres rachunku</button>
            </div>
        </div>
        </div>
        <div class="nav">
            <router-link to="/home/step_three">
            <button> ← Wstecz</button>
            </router-link>
            <div class="right_nav">
                <div v-if="!pickupOption">
                    <button v-if="!emptyValidation()" @click="errorEmptyValidation()">Wypełnij Formularz</button>
                    <button v-if="emptyValidation() && !typeValidation() && !pickupOption" @click="errorValidation()">Popraw Formularz</button>
                    <router-link v-if="emptyValidation() && typeValidation() && !pickupOption"  to="/home/step_four">
                    <button @click="updateDelivery('Wysyłka')">Złóż Zamówienie →</button>
                    </router-link>
                </div>
            <router-link v-if="pickupOption" to="/home/step_four">
            <button @click="updateDelivery('Odbiór Osobisty');">
            Złóż Zamówienie →
            </button>
            </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import { mapMutations } from 'vuex'

export default {
  name: 'StepThreeAddition',
  data: () => ({
    pickupOption: true,
    deliveryOption: false,
    delivery: {
      name: '',
      surname: '',
      adress: '',
      postalCode: '',
      city: '',
      phone: '',
      email: ''
    },
    error: {
      name: false,
      surname: false,
      adress: false,
      postalCode: false,
      city: false,
      phone: false,
      email: false
    }
  }),
  activated () {
    this.pickupOption = true
    this.deliveryOption = false
  },
  updated () {
    if (this.pickupOption) {
      const deliveryPrice = 0
      this.updatePrice(deliveryPrice)
    } else {
      const deliveryPrice = 5
      this.updatePrice(deliveryPrice)
    }
  },
  methods: {
    ...mapMutations([
      'updatePrice'
    ]),
    updateDelivery (choice) {
      if (choice === 'Wysyłka') {
        this.$store.state.Delivery = this.delivery
      }
      this.$store.state.DeliveryChoice = choice
    },
    fillDelivery () {
      this.delivery = this.$store.state.PersonalData
    },
    checkboxLogic () {
      this.deliveryOption = !this.deliveryOption
      this.pickupOption = !this.pickupOption
    },
    errorEmptyValidation () {
      if (!this.delivery.name) {
        document.getElementById('name').style.background = 'tomato'
      } else {
        document.getElementById('name').style.background = 'white'
      }
      if (!this.delivery.surname) {
        document.getElementById('surname').style.background = 'tomato'
      } else {
        document.getElementById('surname').style.background = 'white'
      }
      if (!this.delivery.postalCode) {
        document.getElementById('postalCode').style.background = 'tomato'
      } else {
        document.getElementById('postalCode').style.background = 'white'
      }
      if (!this.delivery.city) {
        document.getElementById('city').style.background = 'tomato'
      } else {
        document.getElementById('city').style.background = 'white'
      }
      if (!this.delivery.email) {
        document.getElementById('email').style.background = 'tomato'
      } else {
        document.getElementById('email').style.background = 'white'
      }
      if (!this.delivery.adress) {
        document.getElementById('adress').style.background = 'tomato'
      } else {
        document.getElementById('adress').style.background = 'white'
      }
    },
    emptyValidation () {
      return this.delivery.postalCode && this.delivery.name && this.delivery.surname && this.delivery.adress && this.delivery.city && this.delivery.email
    },
    errorValidation () {
      if (this.error.name) {
        alert('Popraw imie')
        document.getElementById('name').style.background = 'tomato'
      }
      if (this.error.surname) {
        alert('Popraw nazwisko')
        document.getElementById('surname').style.background = 'tomato'
      }
      if (this.error.city) {
        alert('Popraw miasto')
        document.getElementById('city').style.background = 'tomato'
      }
      if (this.error.email) {
        alert('Popraw e-mail')
        document.getElementById('email').style.background = 'tomato'
      }
      if (this.error.adress) {
        document.getElementById('adress').style.background = 'tomato'
        alert('Popraw Adress')
      }
      if (this.error.phone) {
        document.getElementById('phone').style.background = 'tomato'
        alert('Popraw numer telefonu')
      }
    },
    typeValidation () {
      const phoneValidation = this.delivery.phone.length === 9 || this.delivery.phone.length === 0
      const postalCodeValidation = /^\d\d-\d\d\d$/
      const numberValidation = /^[0-9]+$/
      const letterValidation = /^[A-Za-z]+$/
      const emailValidation = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
      if (!phoneValidation || !emailValidation.test(this.delivery.email) || !letterValidation.test(this.delivery.name) || !letterValidation.test(this.delivery.surname) || !letterValidation.test(this.delivery.city) || !postalCodeValidation.test(this.delivery.postalCode)) {
        if (!letterValidation.test(this.delivery.name)) {
          this.error.name = true
        } else {
          this.error.name = false
        }
        if (!letterValidation.test(this.delivery.surname)) {
          this.error.surname = true
        } else {
          this.error.surname = false
        }
        if (!letterValidation.test(this.delivery.city)) {
          this.error.city = true
        } else {
          this.error.city = false
        }
        if (!emailValidation.test(this.delivery.email)) {
          this.error.email = true
        } else {
          this.error.email = false
        }
        if (!emailValidation.test(this.delivery.postalCode)) {
          this.error.postalCode = true
        } else {
          this.error.postalCode = false
        }
        if (!phoneValidation) {
          this.error.phone = true
        } else {
          this.error.phone = false
        }
        return false
      } else {
        if (this.delivery.postalCode.length === 6) {
          switch (this.delivery.phone.length) {
            case 0:
              return true
            case 9:
              if (numberValidation.test(this.delivery.phone)) {
                this.error.phone = false
                return true
              } else {
                this.error.phone = true
                return false
              }
          }
        } else {
          this.error.postalCode = true
          return false
        }
      }
    }
  }
}
</script>
<style lang="sass" scoped>
.delivery_form
  display: grid
  justify-content: center
  font-size: 1rem
  padding-top: 5px
  input
    margin: 10px
    border-radius: 25px
    border: none
    padding: 5px
.step_three_addition
  font-size: 1.5rem
  display: grid
  height: 30%
  align-items: end
  .container
    display: flex
    justify-content: space-evenly
    align-items: flex-start
    font-weight: bold
.pickup, .delivery
  display: grid
  justify-items: center
  button
    border: white 5px solid
    border-radius: 25px
    color: white
    background: transparent
    cursor: pointer
    padding: 10px
    transition: 1s
    font-weight: bold
    &:hover
      color: #4dde9f
      border: 5px #4dde9f solid
  input
    height: 30px
    width: 30px
.container_height
  max-height: 50vh
.fill_delivery
  position: fixed
  right: 35%
  top: 10%

@media only screen and (max-width: 780px)

.container
  display: flex !important
  flex-direction: row !important
  align-items: center
  gap: 1em
.fill_delivery
  right: unset
.delivery
  button
    position: relative
</style>
